    import tabs from "../../components/common/tabs/tabs.vue";
    export default {
        components:{
            tabs
        },
        created(){
            // 请求服务费接口
            this.getServePrice();
          // 为图片上传添加请求头
            uni.getStorage({
                key:'token',
                success:(res)=>{
                    this.headerData.token=res.data
                }
            })
        },
        data() {
            return {
                URL:getApp().globalData.URL,
                // 界面任务内容 
                taskType:0,    
                // 是否开启添加任务步骤窗口
                pushTaskBox:false,
                // 服务费用
                dytaskServePrice:0,
                taskServePrice:0,
                taskData:{
                  title: "",
                price: "",
                quota: "",
                content: "",  
                },
                //抖音页  是否选择了图片
                isImg: false,
                // 上传路径
                action: 'http://121.42.13.36:9000/api/v1/push_dy_task',
                // 步骤图上传路径
                stepPicAction: 'http://121.42.13.36:9000/api/v1/upload_task_detail_pic',
                formData: {
                    title: '',
                    content: '',
                    price: '',
                    quota: '',
                    dy_url: '',
                },
                headerData: {
                    token: null
                },
                // 悬赏任务步骤数组
                currentPushTaskBoxData:{
                    text:'',
                    pic:'',
                    //悬赏任务页  是否选择了图片
                    isImg: false,
                },
                taskStepList:[
                    
                ]
            }
        },
        methods: {
            async getServePrice(){
               await this.$u.get('/get_serve_price',{name:'push_task'}).then(res=>this.taskServePrice=res.data)
               await this.$u.get('/get_serve_price',{name:'push_dy_task'}).then(res=>this.dytaskServePrice=res.data)
            },
            // 网络请求返回过滤
            response(res){
              if (res.errorCode === 10000) {
                  uni.showToast({
                      title: res.msg,
                      icon: "none"
                  })
                  return false;
              } else if (res.errorCode === 20003) {
                  uni.showToast({
                      title: res.msg,
                      icon: "none"
                  })
                  setTimeout(() => {
                      uni.reLaunch({
                          url: "../login/login"
                      })
                      return false;
                  }, 1500)
              } else {
                  return true;
              }
            },
            // post
            post(){
                if(this.taskType===0){
                    this.postDyTask();
                }
                if(this.taskType===1){
                    this.postTask();
                }
            },
            // tabs 监听
            changeTabIndex(index){
                // 0 抖音  1 普通任务
                this.taskType=index;
            },
            hidePushTaskBox(){
               this.pushTaskBox=false;
            },
            openPushTaskBox(){
               this.pushTaskBox=true;
            },
            imglist(res) {

            },
            dycheckParam() {
                if (this.$u.test.empty(this.formData.title)) {
                    uni.showToast({
                        title: "你还没有填写标题",
                        icon: "none"
                    })
                    return false;
                };
                if (this.$u.test.empty(this.formData.price)) {
                    uni.showToast({
                        title: "你还没有填写点赞价格",
                        icon: "none"
                    })
                    return false;
                };
                if (this.$u.test.empty(this.formData.dy_url)) {
                    uni.showToast({
                        title: "你还没有填写视频链接",
                        icon: "none"
                    })
                    return false;
                };
                if (this.$u.test.empty(this.formData.quota)) {
                    uni.showToast({
                        title: "你还没有填写发布数量",
                        icon: "none"
                    })
                    return false;
                };
                if (this.$u.test.empty(this.formData.content)) {
                    uni.showToast({
                        title: "你还没有填写任务说明",
                        icon: "none"
                    })
                    return false;
                };

                    return true;
            },
            
            postDyTask() {
                if (!this.dycheckParam()) return;
                // 上传
                this.$refs.uUpload.upload();
            },
            taskcheckParam() {
                if (this.$u.test.empty(this.taskData.title)) {
                    uni.showToast({
                        title: "你还没有填写标题",
                        icon: "none"
                    })
                    return false;
                };
                if (this.taskData.title.length > 10 ) {
                    uni.showToast({
                        title: "标题太长了",
                        icon: "none"
                    })
                    return false;
                };
                if (this.$u.test.empty(this.taskData.price)) {
                    uni.showToast({
                        title: "你还没有填写任务价格",
                        icon: "none"
                    })
                    return false;
                };
                if (this.$u.test.empty(this.taskData.quota)) {
                    uni.showToast({
                        title: "你还没有填写发布数量",
                        icon: "none"
                    })
                    return false;
                };
                if (this.$u.test.empty(this.taskData.content)) {
                    uni.showToast({
                        title: "你还没有填写任务说明",
                        icon: "none"
                    })
                    return false;
                };
                // 验证是否有步骤图片
                if (this.$u.test.empty(this.taskStepList)) {
                    uni.showToast({
                        title: "至少添加一个步骤",
                        icon: "none"
                    })
                    return false;
                }
                    return true;
            },
            async postTask(){
                if(!this.taskcheckParam())return;
               await this.$u.post('/push_task',{
                      title:this.taskData.title,
                      price:this.taskData.price,
                      quota:this.taskData.quota,
                      content:this.taskData.content,
                    task_step_list:JSON.stringify(this.taskStepList)
                }).then(res=>{
                    uni.showToast({
                        title: res.msg,
                    }) 
                    setTimeout(()=>{
                       uni.switchTab({
                           url:"../home/home"
                       })
                    },2000)
                    
                })
            },
            oversize() {
                uni.showToast({
                    title: "图片过大，请重新上传",
                    icon: 'none'
                })
            },
            //抖音图片上传成功 任务发布完成
            success(data) {
                let data2 = JSON.parse(data)
                 if(!this.response(data2)) return;
                if (data2.errorCode === 0) {
                    uni.showToast({
                        title: data2.msg
                    })
                    setTimeout(() => {
                        uni.switchTab({
                            url: '../home/home'
                        })
                    }, 1500)
                    return;
                }
                if (data2.errorCode === 20010) {
                    uni.showToast({
                        title: data2.msg,
                        icon: 'none'
                    })
                    setTimeout(() => {
                        uni.switchTab({
                            url: '../home/home'
                        })
                    }, 1500)
                    return;
                }
                if (data2.errorCode === 20005) {
                    uni.showToast({
                        title: data2.msg,
                        icon: 'none'
                    })
                    setTimeout(() => {
                        uni.switchTab({
                            url: '../home/home'
                        })
                    }, 1500)
                    return;
                }
            },
            // 悬赏任务步骤图上传成功
            taskstepPicSuccess(data){
                let data2 = JSON.parse(data)
                if (data2.errorCode === 0) {
                    this.currentPushTaskBoxData.pic=data2.data;
                }
            },
            //图片上传失败
            error(res) {
                uni.showToast({
                    title: "上传遇到未知错误",
                    icon: 'none'
                })

            },
            //抖音页
            complete(list) {
                this.isImg = true
            },
            remove(index) {
                this.isImg = false
            },
            // 悬赏任务页
            taskComplete(list) {
                this.currentPushTaskBoxData.isImg = true
            },
            taskRemove(index) { 
                this.currentPushTaskBoxData.isImg = false
            },
            
            // 添加步骤
            addSetp(){
               let text=this.$u.test.empty(this.currentPushTaskBoxData.text)
             let pic=this.$u.test.empty(this.currentPushTaskBoxData.pic)
if(!(text&&pic)){
    // 追加list
    let obj=JSON.parse(JSON.stringify(this.currentPushTaskBoxData))
    this.taskStepList.push(obj)
    this.hidePushTaskBox();
    this.currentPushTaskBoxData.text="";
    this.currentPushTaskBoxData.pic="";
    this.currentPushTaskBoxData.isImg=false;
   this.$refs.uploadPushBox.lists = []
}else{
   uni.showToast({
       title:"请完善再添加",
       icon:'none'
   })
}
               
            }
        },
        computed: {
            payment() {

                if(this.taskType===0){
                   return (this.formData.price * this.formData.quota) + (this.dytaskServePrice * this.formData.quota); 
                }
                if(this.taskType===1){
                    return (this.taskData.price * this.taskData.quota) + (this.taskServePrice * this.taskData.quota); 
                }
                
            }
        }

    }
